@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
footer {
    display: flex;
    background-color: var(--baby-blue);
    border-radius: 50% 50% 0 0;
    margin-top: 100px;
}

/* Text */
.footer .header {
    color: #3f3f3f;
    font-weight: bold;
}
.footer .text {
    color: #3a2a2a; 
    font-size: 13;
}

/* Icons */
.facebook-icon {color: #4968ad}
.instagram-icon {margin: 0 1rem; color: #e95f62}
.twitter-icon {margin: 0 1rem; color: #49a1eb}

/* Telephone Link */
.tel-link {
    color: var(--red);
    font-weight: bold;
    border-bottom: 1px solid black;
    text-decoration: none !important;
}
.tel-link:hover  {
    color: var(--dark-red);
}
.navbar {
    padding-right: 6em !important; 
    padding-left: 6em !important;
}
.navbar > img {width: 45px; height: 45px}

.nav-link {
    color: #9c9d9c !important;
}
.nav-link:hover {
    color: black !important;
}
.nav-link-active {
    color: black !important;
}
.row img {object-fit: cover}

.icon-col {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    text-align: center;
}
.icon-col p {font-size: 18px}

.circle {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--red);
    color: white;
    font-size: 64px;
    width: 100px;
    height: 100px;
    padding: 1.5rem;
    border-radius: 50%;
}

/* Main Header */
.main-header {
    min-height: 90vh;
    max-height: 90vh;
}
.main-header >* h3 {font-size: 3.5rem}
.main-header > div {margin: 0 !important}

/* Scroll Reminder */
.arrow {
    font-size: 18px;
    color: var(--red) !important;
    -webkit-animation: arrow 3s linear 1s infinite;
            animation: arrow 3s linear 1s infinite;
}
@-webkit-keyframes arrow {
    0%   {transform: translateY(0px)}
    10% {transform: translateY(3px)}
    25% {transform: translateY(0px)}
    100% {transform: translateY(0px)}
}
@keyframes arrow {
    0%   {transform: translateY(0px)}
    10% {transform: translateY(3px)}
    25% {transform: translateY(0px)}
    100% {transform: translateY(0px)}
}

/* File Input */
.io-border {
    border: 2px solid var(--grey);
    width: 90%;
    transition: all 1s;
    opacity: 0;
}
.io-border.show {
    opacity: 1;
}

.file-icon {
    font-size: 25px;
    color: #9ca5b6;
}
svg {font-size: 64px}
h4 {font-style: italic}
.question {
    background-color: var(--baby-blue); 
    border-radius: 35px;
}
body .desktop {
    background-color: #f8f9ff !important;
}
.menubar {
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    background-color: var(--color0);
}
.link {
    color: var(--color1) !important;
}
.link-active {
    color: var(--color3) !important;
}
.mobile .nav-pills {
    background-color: var(--color0) !important;
    border-radius: 1rem;
}

.mobile .nav-link { 
    margin: .5rem;
    background-color: var(--color0);
    color: var(--color1) !important;
    border-radius: 1rem !important;
}
.mobile .nav-link.active {
    background-color: white !important;
    color: var(--color3) !important;
    font-weight: bold;
    box-shadow: 0 .125rem 1rem rgba(0,0,0,.075);
}

.mobile .tab-content {
    padding: 1.8rem 1.5rem;
}
:root {
    --color0: #f3f9ff;
    --color1: #b7c9dc;
    --color2: #8286a3; 
    --color3: #4a4d5f;
}

body .mobile {
    background-color: white;
}

.mobile h1, h2, h3, h4, h5, h6 {
  color: #4a4d5f;
  color: var(--color3)
}

.mobile p, small {
    color: #8286a3;
    color: var(--color2)
}

.mobile .btn {border-radius: 1rem !important}
:root {
    --grey: #d5e1f8;
    --dark-grey: #c1d2f4 ;
    --grey-text: #9ca5b6;
    --red: #e95f62;
    --dark-red: #e75053;
    --yellow: #fa9746;
    --baby-blue: #dde8fc;
    --blue: #6495ed;
}

body {
    font-family: 'Quicksand', sans-serif !important;
}

/* Colors */
.blue {color: #6495ed;color: var(--blue)}
.bg-baby-blue {background-color: #dde8fc;background-color: var(--baby-blue)}
.bg-grey {background-color: #d5e1f8;background-color: var(--grey)}

/* Buttons */
.btn {border: none !important}
.btn:focus {box-shadow: none !important}
.btn {
    border-radius: 20px !important;
    background-color: #d5e1f8 !important;
    background-color: var(--grey) !important;
    color: #9ca5b6 !important;
    color: var(--grey-text) !important;
}
.btn:hover {
    background-color: #c1d2f4 !important;
    background-color: var(--dark-grey) !important;
}

.btn.red {
    background-color: #e95f62 !important;
    background-color: var(--red) !important;
    color: white !important;
}
.btn.red:hover {
    background-color: #e75053 !important;
    background-color: var(--dark-red) !important;
}

.btn.blue {
    background-color: #6495ed !important;
    background-color: var(--blue) !important;
    color: white !important;
}
.btn.blue:hover {
    background-color: #6495ed !important;
    background-color: var(--blue) !important;
}
